import shareThis from 'share-this'
import * as twitterSharer from "./share-this/twitter";
import * as facebookSharer from "./share-this/facebook";
import * as redditSharer from "./share-this/reddit";


const selectionShare = shareThis(shareThisConfig())

export default {
  init (config, context) {
    if (!window.matchMedia
            || !window.matchMedia("(pointer: coarse)").matches) {
        hidePopoverControl();
        selectionShare.init();
    }
  }
}
document.addEventListener("mouseup", function(event) {
  hidePopoverControl()
});

function hidePopoverControl(){
  var shareThisPopover = document.getElementsByClassName("share-this-popover")
  var selectedText = ""
  if(shareThisPopover.length > 0){
    selectedText = getSelectionText()
    selectedText = selectedText.substring(0, selectedText.length-2)
    if(/\r|\n/.exec(selectedText) || selectedText == "") {
      shareThisPopover[0].remove()
      return true
    }else{
      return false
    }
  }
}

function getSelectionText() {
    var text = "";
    if (window.getSelection) {
        text = window.getSelection().toString();
    } else if (document.selection && document.selection.type != "Control") {
        text = document.selection.createRange();
    }
    return text;
}

function shareThisConfig(){
  return {
      selector: ".article__data",
      sharers: [twitterSharer,facebookSharer,redditSharer ],
      onOpen(popover) {
        if(!hidePopoverControl()){
          popover.style.maxWidth = "none";
          popover.style.marginTop = "-20px";
          const range = getSelection().getRangeAt(0);
          const rect = range.getBoundingClientRect();
          for (const prop of [ "right", "left", "width", "height"]) {
            popover.style[prop] = rect[prop] + "px";
          }
        }
    }
  }
}
